import { CheckIcon, XIcon } from 'lucide-react';
import React from 'react'


const validationRules = [
  { id: 'length', text: 'At least 8 characters', regex: /.{8,}/ },
  { id: 'number', text: 'At least 1 number', regex: /\d/ },
  { id: 'lowercase', text: 'At least 1 lowercase letter', regex: /[a-z]/ },
  { id: 'uppercase', text: 'At least 1 uppercase letter', regex: /[A-Z]/ },
  { id: 'special', text: 'At least 1 special character', regex: /[^A-Za-z0-9]/ },
];


const ValidationItem = ({ isValid, text }: { isValid: boolean, text: string }) => (
  <li className={`flex items-center transition-colors  duration-300 text-[12px] text-gray-400 ${isValid ? 'text-green-500 ' : 'text-muted-foreground'}`}>
    {isValid ? <CheckIcon  className="h-4 w-4 mr-2" /> : <XIcon className="h-4 w-4 mr-2" />}
    <span>{text}</span>
  </li>
);

export const PasswordValidation: React.FC<{ password: string }> = ({ password }) => {
    const [validationResults, setValidationResults] = React.useState<{ [key: string]: boolean }>({});
    React.useEffect(() => {
        const results: { [key: string]: boolean } = {};
        validationRules.forEach(rule => {
            results[rule.id] = rule.regex.test(password);
        });
        setValidationResults(results);
    }, [password]);

    return (
        <ul className="mt-2 space-y-1 grid grid-cols-1  lg:grid-cols-2 funnel-sans-text">
            {validationRules.map(rule => (
                <ValidationItem
                    key={rule.id}
                    isValid={validationResults[rule.id] || false}
                    text={rule.text}
                />
            ))}
        </ul>
    );
};